      pathElement.setAttribute('stroke-linejoin', this.ljEnum[data.lj] || 'round'); ////pathElement.style.strokeLinejoin = this.ljEnum[data.lj] || 'round';

      pathElement.setAttribute('fill-opacity', '0'); ////pathElement.style.fillOpacity = 0;

